import React, { Suspense } from 'react';
import { Route, Redirect, Switch } from 'react-router-dom';

const Application = React.lazy(() =>
  import(
    /* webpackChunkName: "challenges-applicaiton-view" */ './application-view'
  )
);
const Basics = React.lazy(() =>
  import(/* webpackChunkName: "challenges-basics-view" */ './basics-view')
);
const Brands = React.lazy(() =>
  import(/* webpackChunkName: "challenges-brands-view" */ './brands-view')
);
const Dates = React.lazy(() =>
  import(/* webpackChunkName: "challenges-dates-view" */ './dates-view')
);
const DefaultView = React.lazy(() =>
  import(/* webpackChunkName: "challenges-default-view" */ './default-view')
);
const Faq = React.lazy(() =>
  import(/* webpackChunkName: "challenges-faq-view" */ './faq-view')
);
const Judges = React.lazy(() =>
  import(/* webpackChunkName: "challenges-judges-view" */ './judges-view')
);
const Links = React.lazy(() =>
  import(/* webpackChunkName: "challenges-links-view" */ './links-view')
);
const Prizes = React.lazy(() =>
  import(/* webpackChunkName: "challenges-prizes-view" */ './prizes-view')
);
const Sponsors = React.lazy(() =>
  import(/* webpackChunkName: "challenges-sponsors-view" */ './sponsors-view')
);
const Details = React.lazy(() =>
  import(/* webpackChunkName: "challenges-details-view" */ './details')
);

const Challenges = ({ match }) => (
  <Suspense fallback={<div className="loading" />}>
    <Switch>
      <Redirect exact from={`${match.url}/`} to={`${match.url}/view-all`} />

      <Route
        path={`${match.url}/view-all`}
        render={(props) => <DefaultView {...props} />}
      />

      <Route
        path={`${match.url}/application/:id?`}
        render={(props) => <Application {...props} />}
      />
      <Route
        path={`${match.url}/basics/:id?`}
        render={(props) => <Basics {...props} />}
      />
      <Route
        path={`${match.url}/brands/:id?`}
        render={(props) => <Brands {...props} />}
      />
      <Route
        path={`${match.url}/dates/:id?`}
        render={(props) => <Dates {...props} />}
      />
      <Route
        path={`${match.url}/faq/:id?`}
        render={(props) => <Faq {...props} />}
      />
      <Route
        path={`${match.url}/judges/:id?`}
        render={(props) => <Judges {...props} />}
      />
      <Route
        path={`${match.url}/links/:id?`}
        render={(props) => <Links {...props} />}
      />
      <Route
        path={`${match.url}/prizes/:id?`}
        render={(props) => <Prizes {...props} />}
      />
      <Route
        path={`${match.url}/sponsors/:id?`}
        render={(props) => <Sponsors {...props} />}
      />
      <Route
        path={`${match.url}/details`}
        render={(props) => <Details {...props} />}
      />

      <Redirect to="/error" />
    </Switch>
  </Suspense>
);

export default Challenges;
